//
// Imports
//
import * as navutils from './navigation-utils.js';

const $ = jQuery;

//
// Accessibility via Keyboard controls

// Keyboard Navigation Overview
// ----------------------------
// Key right: go to parent link to the right
// Key left: go to parent link to the left
// Key down: if parent link, go to first child link. If child link, go to next child link. If last child link, do nothing
// Key up: if child link, go to prior child link or parent link if first.
// Key esc: If child link, go to parent. If parent link, leave menu.
//
//
export const navKeyboardControl = () => {

  const KEYED = navutils.KEYED;

  $('#site-menu a:first').focusin(() => {
    if($('html').hasClass('keyboard-user')) {
      $('#site-nav-instructions').removeClass('sr-only');
    }
  }).focusout( () => {
    $('#site-nav-instructions').addClass('sr-only');
  });

  $('#site-menu a').on('keydown', function (e) {

    // Bail if there is an open modal
    if (window.siteHasOpenModal) {
      return;
    }

    const KEY_CODE = e.keyCode;

    // Don't do the normal thing
    if (KEY_CODE === KEYED.left || KEY_CODE === KEYED.up || KEY_CODE === KEYED.right || KEY_CODE === KEYED.down || KEY_CODE === KEYED.esc) {
      e.preventDefault();
    }

    const $this       = $(this),
          parentItem  = $this.parent('li'),
          thisSubMenu = parentItem.find('.submenu'),
          hasSubMenu  = thisSubMenu.length > 0,
          isChild     = $this.closest('.submenu').length > 0;

    //
    // Key RIGHT
    //
    if (KEY_CODE === KEYED.right) {

      // Child Link
      if (isChild) {

        let parentSubMenu = $this.closest('.submenu');
        let cols = parentSubMenu.find('ul').length;

        // If multiple lists
        if (cols > 1) {
          if ($this.closest('ul').index() + 1 === cols) {
            navutils.focusNextTopLevelLink(parentSubMenu.closest('li'));
          } else {
            $this.closest('ul').next('ul').find('a').first().focus();
          }

          // Go to parents next link
        } else {
          navutils.focusNextTopLevelLink($this.closest('.submenu').closest('li'));
        }

        // Parent Link
      } else {
        navutils.focusNextTopLevelLink(parentItem);
      }


    //
    // Key LEFT
    //
    } else if (KEY_CODE === KEYED.left) {
      // Go to parent link to the left;
      if (isChild) {

        let parentSubMenu = $this.closest('.submenu');
        let cols = parentSubMenu.find('ul').length;

        // If multiple lists
        if (cols > 1) {
          if ($this.closest('ul').index() === 0) {
            navutils.focusPrevTopLevelLink(parentSubMenu.closest('li'));
          } else {
            $this.closest('ul').prev('ul').find('a').first().focus();
          }

          // Go to parents previous link
        } else {
          navutils.focusPrevTopLevelLink($this.closest('.submenu').closest('li'));
        }
      } else {

        if (!parentItem.prev('li').length) {
          if ($this.closest('ul').prevAll('#site-menu').length) {
            $this.closest('ul').prevAll('#site-menu').find('.site-header__main-nav-item').last().find('a').first().focus();
          }
        } else {
          navutils.focusPrevTopLevelLink(parentItem);
        }


      }

    //
    // Key DOWN
    //
    } else if (KEY_CODE === KEYED.down) {

      // Parent link - go to first child link
      if (hasSubMenu) {
        parentItem
          .find('.submenu a')
          .first()
          .focus();

        // Child Link
      } else if (isChild) {

        // If last, go to parents next link
        if (parentItem.next('li').length === 0) {
          return false;

          // Go to next child link.
        } else {
          parentItem
            .next('li')
            .find('a')
            .focus();
        }
      }

    //
    // Key UP
    //
    } else if (KEY_CODE === KEYED.up) {

      // Child Link
      if (isChild) {

        // If last, go to parents next link
        if (parentItem.prev('li').length === 0) {
          return;

          // Go to previous child link.
        } else {
          parentItem
            .prev('li')
            .find('a')
            .focus();
        }
      }

    //
    // Key ESCAPE
    //
    } else if (KEY_CODE === KEYED.esc) {
      // Go back to parent link
      if (isChild) {
        $this.closest('.submenu')
          .closest('li')
          .find('a')
          .first()
          .focus();
      } else {
        navutils.closeSubNavs();
        $('#site-more-links').find('button').first().focus();

      }
    }

  });
};



