//
// Imports
//
import '../../vendor/slick.js';

const getHashValue = (key) => {
  var matches = location.hash.match(new RegExp(key + '=([^&]*)'));
  return matches ? matches[1] : null;
}

window.siteHasOpenModal = false;

const $ = jQuery;

const modalAnimTime  = 550,
      $w             = $(window),
      $opts          = $('#js__site-options'),
      mobileCarousel = $('.flyout--mobile-carousel'),
      slickOpts = {
        dots: true,
        arrows: false,
        infinite: false,
        accessibility: true,
        appendDots: $('.flyout--more__dots'),
      };


//
// Open the Modal
// --------------
// @param {object} trigger - the clicked object (button)
//

let focusableEls     = null,
    firstFocusableEl = null,
    lastFocusableEl  = null;

const openModal = (triggerEl) => {

  let $modal = $(triggerEl.data('target'));
  let modalData = triggerEl.data('target');

  $('body').addClass('menu-modal-open modal--' + $modal.attr('id'));

  $modal.addClass('modal__open').attr('aria-modal', true);

  if ( $(window).width() < 750 ) {
    $('.flyout--mobile-carousel').slick(slickOpts);
  }

  setTimeout(() => {
    $modal.addClass('modal__animates-in');
  }, 20);

  // Return to button when modal is closed
  triggerEl.addClass('modal--return-focus modal--open-modal');

  // Let the window know a modal is open
  window.siteHasOpenModal = true;

  // Animate modal sub items
  $modal.find('.u-modal-watch').addClass('u-modal-in-view');

  // Check if we should focus on search input
  if(modalData === '#js__flyout-search'){
    $("#flyout__search-input").focus();
  } else {
  // Focus on the first close element
  $modal.find('.modal--close')
    .first()
    .focus();
  }
  // Setup for tab trap
  focusableEls = $modal.find('a, object, :input, iframe, [tabindex]');
  firstFocusableEl = focusableEls.first()[0];
  lastFocusableEl = focusableEls.last()[0];

};


//
// Close the modal
// ---------------
// @param {object} trigger the clicked object
// @param {string} type the type of click - `keyed` or `click`
//
const closeModal = (trigger) => {

  let $returnFocus = $('.modal--return-focus'),
      $modal       = trigger.closest('.modal');

  $('body').removeClass('menu-modal-open modal--' + $modal.attr('id'));

  $modal.removeClass('modal__animates-in');

  setTimeout(() => {
    if ( $('.flyout--mobile-carousel').hasClass('slick-initialized') ) {
      $('.flyout--mobile-carousel').slick('unslick');
    }
    $modal.removeClass('modal__open').attr('aria-modal', false);
  }, 400);

  // Return focus, switch aria-label
  $returnFocus.removeClass('modal--return-focus modal--open-modal')
    .attr('aria-label', $returnFocus.attr('data-arialabel'))
    .focus();

  // Modal is closed so set global to false
  window.siteHasOpenModal = false;

  // Reset tab trab vars
  focusableEls = null;
  firstFocusableEl = null;
  lastFocusableEl = null;

};


//
// Modal functionality
//
export const modalNavigation = () => {

  window.siteHasOpenModal = false;

  //
  // Open the modal on menu button click
  //
  $('.modal--open').on('click', function() {
    const $this = $(this);

    // If there is a modal already open
    if (window.siteHasOpenModal) {
      const target = $($this.attr('data-target'));

      // If trigger to open is clicked but an existing modal is open,
      // close the open modal, then open target modal.
      if (!target.hasClass('modal__open')) {
        $('.modal__open').find('.modal--close').trigger('click');
        setTimeout(function() {
          openModal($this);
        }, modalAnimTime);

      // Otherwise close the target modal (used in mobile)
      } else {
        target.find('.modal--close').trigger('click')
      }

    // Otherwise just open the modal
    } else {
      openModal($this);
    }

    $this.attr('aria-label', 'Close Modal');

  });


  //
  // Close the modal on modal close button click
  //
  $('.modal--close').on('click', function() {
    closeModal($(this));
  });


  //
  // Modal Key Functions
  //
  $(window).on('keydown', function(e) {

    // Allow Escape to close window modal
    if (e.keyCode === 27  ) {
      if ( window.siteHasOpenModal ) {
        $('.modal.modal__open').find('.modal--close').first().trigger('click');
      }

    // Tab Trap on open modals
    } else if (
      (e.key === 'Tab' || e.keyCode === 9) &&
      window.siteHasOpenModal &&
      ($('.modal.modal__open').attr('aria-modal') === 'true')
    ) {
      // shift + tab
      if ( e.shiftKey ) {
        if (document.activeElement === firstFocusableEl) {
          lastFocusableEl.focus();
          e.preventDefault();
        }
      // tab
      } else {
        if (document.activeElement === lastFocusableEl) {
          firstFocusableEl.focus();
          e.preventDefault();
        }
      }
    }
  });


  //
  // SEARCH MODAL
  //
  // If search has a value in it set by a Swiftype param, add the animation class
  if (getHashValue('stq')) {
    $('#flyout__search-label').addClass('active-input');
  }

  // Animate Search Label on Focus
  $('#flyout__search-input').on('focusin', function() {
    $('#flyout__search-label').addClass('active-input');
  }).on('focusout', function() {
    if (!$(this).val()) {
      $('#flyout__search-label').removeClass('active-input');
    }
  });

}



