//
// Imports
//
import { closeOptions } from './navigation-options.js';
import * as navutils from './navigation-utils.js';

const $ = jQuery;

export const scrollNav = () => {

  let $nav      = $('.site-header');
  let $body     = $('body');
  let navHeight = $nav.height();
  let scrolled  = $(window).scrollTop();
  let position  = scrolled;

  const stuckClass = 'site-header__hide';

  const setHeader = () => {

    if ($('body').hasClass('menu-modal-open')) {
      return false;
    }

    // Stick the nav
    if ( scrolled > (navHeight)) {
      // Scrolling Up
      if (scrolled < position ) {
        $body.removeClass(stuckClass);
      // Scrolling down
      } else {
        navutils.closeSubNavs(false, true);
        $body.addClass(stuckClass);
        closeOptions();
      }

    // Unstick the nav
    } else {
      $body.removeClass(stuckClass);
    }

    position = scrolled;
  }

  setHeader();

  $(window).on('scroll', function() {
    scrolled = $(window).scrollTop();
    setHeader();
  });

  $(window).on('load', function () {
    navHeight = $nav.height();
  });

  $(window).on('resize', function() {
    navHeight = $nav.height();
  });
}



